    function mostrarFormulario( editar = false, curriculum = {} ) {//por default no estas editando la curriculum, pero si esta en true, si lo estas editando
        console.log(curriculum);
        const modal = document.createElement('DIV');
        modal.classList.add('modal-formulario');
        modal.classList.add('contenedor');
        modal.innerHTML = `
            <form class="formulario curriculum">
                <legend class="decoracion-titulos">${editar ? 'Editar datos del curriculum' : 'Añade una nueva curriculum'}</legend>
                <div class="form-input">
                    <label for="perfil">Perfil</label>
                    
                    <textarea 
                        required 
                        pattern="[A-Za-z 0-9]+" 
                        id="perfil"
                        minlength="3"
                        title="Escriba algo" 
                        placeholder="${curriculum.perfil ? 'Editar perfil' : 'Añadir perfil'}" 
                        name="perfil" >${curriculum.perfil ? curriculum.perfil : ''}</textarea>
                </div>

                <div class="form-input">
                    <label for="empresa">Empresa</label>
                    
                    <textarea 
                        required 
                        pattern="[A-Za-z 0-9]+" 
                        id="empresa"
                        minlength="3"
                        title="Escriba algo" 
                        placeholder="${curriculum.empresa ? 'Editar empresa' : 'Añadir empresa'}" 
                        name="empresa" >${curriculum.empresa ? curriculum.empresa : ''}</textarea>
                </div>

                <div class="form-input">
                    <label for="empresa_info">Información de la Empresa </label>
                    
                    <textarea 
                        required 
                        pattern="[A-Za-z 0-9]+" 
                        id="empresa_info"
                        minlength="3"
                        title="Escriba algo" 
                        placeholder="${curriculum.empresa_info ? 'Editar la información de la empresa' : 'Añadir empresa_info'}" 
                        name="empresa_info" >${curriculum.empresa_info ? curriculum.empresa_info : ''}</textarea>
                </div>

                <div class="form-input">
                    <label for="proyecto_info">Información del Proyecto</label>
                    
                    <textarea 
                        required 
                        pattern="[A-Za-z 0-9]+" 
                        id="proyecto_info"
                        minlength="3"
                        title="Escriba algo" 
                        placeholder="${curriculum.proyecto_info ? 'Editar Información del Proyecto' : 'Añadir proyecto_info'}" 
                        name="proyecto_info" >${curriculum.proyecto_info ? curriculum.proyecto_info : ''}</textarea>
                </div>



                
                <div class="opciones">
                    <input 
                        type="submit" 
                        class="boton-pri submit-nueva-curriculum" 
                        value="${curriculum.perfil ? 'Guardar Cambios' : 'Añadir al Curriculum'} " 
                    />
                    <button type="button" class="boton-transparente cerrar-modal">Cancelar</button>
                </div>
            </form>
        `;//en value le estas pasando el nombre de la curriculum, para que no la vuelva a escribir el usuario cuando quiera actualizarlo

        setTimeout(() => {
            const formulario = document.querySelector('.modal-formulario');
            formulario.classList.add('animar');

            const pantallaCompleta = document.querySelector(".pantallaCompleta");
            const body = document.querySelector(".body");

            body.classList.add('fijar-body');
            pantallaCompleta.classList.add('activarPantallaNegra');

        }, 0);//se ejecuta la animacion para animar el formulario, se ejecuta en cero segundos

        modal.addEventListener('click', function(e) {
            e.preventDefault();
            //si en el modal le das click a algo, y contiene la clase (cerrar modal) (LO DETECTA DESPUES QUE JAVASCRIPT GENERO EL HTML)
            if(e.target.classList.contains('cerrar-modal')) {
                const formulario = document.querySelector('.modal-formulario');
                formulario.classList.add('cerrar');//esto hace animacion de cerrar modal

                const pantallaCompleta = document.querySelector(".pantallaCompleta");
                const body = document.querySelector(".body");

                body.classList.remove('fijar-body');
                pantallaCompleta.classList.remove('activarPantallaNegra');


                setTimeout(() => {
                    // alert('hola')
                    modal.remove();//remueve ese inerthtml despues de .5 segundos
                }, 500);//se ejecuta la animacion para animar el formulario, se ejecuta en .5 segundo
            } 

            //si en el modal le das click a algo, y contiene la clase (submit-nueva-curriculum) (LO DETECTA DESPUES QUE JAVASCRIPT GENERO EL HTML)
            if(e.target.classList.contains('submit-nueva-curriculum')) {
                const curriculumPerfil = document.querySelector('#perfil').value.trim();//es el input donde pones tu curriculum....(VALUE lee el valor lo que se escribe en el input y TRIM quita los espacios al principio y al final)
                const curriculumEmpresa = document.querySelector('#empresa').value.trim();
                const curriculumEmpresaInfo = document.querySelector('#empresa_info').value.trim();
                const curriculumProyectoInfo = document.querySelector('#proyecto_info').value.trim();


                if(curriculumPerfil === '') {
                    // Mostrar una alerta de error
                    mostrarAlerta('Todos los campos son obligatorios', 'error', document.querySelector('.formulario legend'));
                    return;//no se ejecuta las siguientes lineas de codigo
                } 

                if(curriculumEmpresa === '') {
                    // Mostrar una alerta de error
                    mostrarAlerta('Todos los campos son obligatorios', 'error', document.querySelector('.formulario legend'));
                    return;//no se ejecuta las siguientes lineas de codigo
                } 

                if(curriculumEmpresaInfo === '') {
                    // Mostrar una alerta de error
                    mostrarAlerta('Todos los campos son obligatorios', 'error', document.querySelector('.formulario legend'));
                    return;//no se ejecuta las siguientes lineas de codigo
                } 

                if(curriculumProyectoInfo === '') {
                    // Mostrar una alerta de error
                    mostrarAlerta('Todos los campos son obligatorios', 'error', document.querySelector('.formulario legend'));
                    return;//no se ejecuta las siguientes lineas de codigo
                } 

                // if(curriculumPerfil === '', curriculumEmpresa === '', curriculumEmpresaInfo === '', curriculumProyectoInfo === '') {

                //     return;//no se ejecuta las siguientes lineas de codigo
                // } 


                if(editar) {
                    curriculum.perfil = curriculumPerfil;//aqui se actualiza el nombre de la curriculum (reescribe esa parte del objeto, que es la copia)
                    curriculum.empresa = curriculumEmpresa;
                    curriculum.empresa_info = curriculumEmpresaInfo;
                    curriculum.proyecto_info = curriculumProyectoInfo;
                    actualizarcurriculum(curriculum);
                }
                
            }
        })

        document.querySelector('.poner-formulario').appendChild(modal);
    }

